import { _decorator, Component, Node, VerticalTextAlignment, Label, Vec3, Color } from 'cc';
const { ccclass, property } = _decorator;
import { SudokuCreator } from '@algorithm.ts/sudoku';

@ccclass('GameController')
export class GameController extends Component {
    @property(Node)
    boardNode: Node;
    @property(Label)
    numberLabels: Label[] = [];

    onLoad() {
        this.createPuzzleBoard();
    }


    createPuzzleBoard(){
        let sudokuCreator = new SudokuCreator({childMatrixWidth: 3});
        let boardData = sudokuCreator.createSudoku(0.2);
        let solution = boardData.solution;
        let puzzleGame: number[] = [];
        let solutionGame: number[] = [];
        

        for (let i = 0; i < boardData.puzzle.length; i++) {
            puzzleGame.push(boardData.puzzle[i] + 1);
        }
        for (let i = 0; i < boardData.solution.length; i++) {
            solutionGame.push(boardData.solution[i] + 1);
        }

        console.log(puzzleGame);
        console.log(solutionGame);

        for (let i = 0; i < 9; i++) {
            for (let j = 0; j < 9; j++) {
                const value = puzzleGame[i*9 + j];
                const nodeSudoku = new Node("ValueGame");
                nodeSudoku.setPosition(i * 55, j * 55);
                this.boardNode.addChild(nodeSudoku);
                
                const labelBlock = nodeSudoku.addComponent(Label);
                labelBlock.string = value === 0 ? "-" : value.toString();
                //labelBlock.color = new Color(0, 255, 0);
                

            }
        }

        for (let i = 0; i < 9; i++) {
            const nodeNum = new Node("NumberValueLabel");
            const label = nodeNum.addComponent(Label);
            label.string = `${i + 1}`;
            label.verticalAlign = VerticalTextAlignment.CENTER;
            nodeNum.parent = this.node;
            nodeNum.position = new Vec3(i % 3 * 100 + 180, Math.floor(i / 3) * -110, 0);
            this.numberLabels.push(label);
            nodeNum.on(Node.EventType.TOUCH_END, () => {
                console.log(`Value ${label.string}`);
            });
        }

    }   
}

